import React, {Component} from 'react'
import styled from "styled-components"
import Helmet from "react-helmet"
import Header from "../components/header"
import Footer from "../components/footer"
import SEO from "../components/seo"
import ScrollAnimation from 'react-animate-on-scroll'
import { DiPython, } from "react-icons/di";


const MainContainer = styled.div`
	background: linear-gradient(220deg, #0f2027, #203a43, #2c5364);
	background-size: 300% 300%;

	-webkit-animation: Gradient 7s ease infinite;
	-moz-animation: Gradient 7s ease infinite;
	animation: Gradient 7s ease infinite;

	@-webkit-keyframes Gradient {
		0%{background-position:92% 0%}
		50%{background-position:9% 100%}
		100%{background-position:92% 0%}
	}
	@-moz-keyframes Gradient {
		0%{background-position:92% 0%}
		50%{background-position:9% 100%}
		100%{background-position:92% 0%}
	}
	@keyframes Gradient {
		0%{background-position:92% 0%}
		50%{background-position:9% 100%}
		100%{background-position:92% 0%}
	}
`

const SubContainer = styled.div`
	width: 100%;
	height: 100vh;
	position: relative;
	color: white;
	box-sizing: border-box;
`

const CenterContainer = styled.div`
	position: absolute;
	top: 50%;
	transform: translate(0%, -50%);
	width: 100%;
`

const LeftContainer = styled.div`
	display: inline-block;
	width: 50%;
	box-sizing: border-box;

	@media (max-width: 1080px) {
		width: 100%;
		display: block;
		text-align: center;
	}
`

const RightContainer = styled.div`
	display: inline-block;
	width: 50%;
	box-sizing: border-box;
	padding: 20px;
	position: absolute;
	top: 50%;
	transform: translate(0%, -50%);

	@media (max-width: 1080px) {
		width: 100%;
		text-align: center;
		position: relative;
		transform: none;
		display: block;
	}
`

const CircleImageContainer = styled.div`
	width: 160px;
	height: 160px;
	border-radius: 50%;
	margin-right: 80px;
	background-image: url('/profile.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	float: right;
	display: inline-block;

	@media (max-width: 1080px) {
		float: none;
		margin: 0;
	}
`

const SubTitleContainer = styled.div`
	font-size: 32px;
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
	font-weight: 600;
`

const SubContentContainer = styled.div`
	font-family: Noto Sans KR, Helvetica,Arial, sans-serif;
`

const LiContainer = styled.div`
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
`

const RelativeContainer = styled.div`
	position: relative;
	width: 100%;
`

const HorizonalCenterContainer = styled.div`
	text-align: center;
`

const IconContainer = styled.div`
	
`

export default () => (
	<div>
		<Header />
		<MainContainer>
			<SEO title={`I'm JustKode!`} description={`98년생 학생 프로그래머의 이야기.`} />
			<SubContainer>
				<CenterContainer>
					<RelativeContainer>
						<LeftContainer>
							<div className="animated fadeIn">
								<CircleImageContainer></CircleImageContainer>
							</div>
						</LeftContainer>
						<RightContainer>
							<div className="animated fadeIn delay-1s">
								<SubTitleContainer>I'm justkode!</SubTitleContainer>
								<SubContentContainer>
									안녕하세요, 경희대학교 컴퓨터공학과에 재학 중인 <strong>박민재</strong>라고 합니다. (군 복무중)<br/><br/>
									<LiContainer>- Born: 1998.07.15</LiContainer>
									<LiContainer>- Favorite Language: <strong>Python</strong>, C++, Javascript</LiContainer>
									<LiContainer>- Interasted in: <strong>Deep Learning</strong>, Big Data, Cloud Computing</LiContainer>
									<LiContainer>- Email: justkode@kakao.com</LiContainer>
								</SubContentContainer>
							</div>
						</RightContainer>
					</RelativeContainer>
				</CenterContainer>
			</SubContainer>
		</MainContainer>
		<Footer />
	</div>
)